







































import Vue from 'vue'
import {defineComponent, ref, onMounted} from '@vue/composition-api'

export default defineComponent({
  name: 'App',
  setup(_, {root}) {
    const dark = ref(root.$vuetify.theme.dark)

    function toggleTheme() {
      root.$vuetify.theme.dark = !root.$vuetify.theme.dark
    }

    return {
      dark,
      toggleTheme
    }
  }
});
